import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';
import Logo from '../Logo/index';



export default class Nav extends Component {
    state = {
        route: 'home'};

    handleNavClick = (e) => {
        let temp = e.target.innerHTML;
        this.setState(() => ({route: temp}));
        console.log(this.state.route);
    };


    render () {

            return (
                <div>
                <div className='uk-visible@l uk-container uk-position-bottom-right uk-flex uk-flex-column uk-text-center uk-margin-large-right uk-margin-large-bottom'>
                    <Logo link='/home' size='100px'/>
                    <ul className="uk-nav">
                        <li>
                            <NavLink activeClassName='NavLink-active' onClick={this.handleNavClick} to='/web'>web</NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName='NavLink-active' onClick={this.handleNavClick}
                                     to='/data_viz'>data_viz</NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName='NavLink-active' onClick={this.handleNavClick}
                                     to='/about'>about</NavLink>
                        </li>
                    </ul>
                </div>


                    <div className="uk-hidden@l uk-position-fixed uk-position-z-index">

                        <nav className="uk-navbar-container" data-uk-navbar="dropbar: true; dropbar-mode: push">

                            <div className="uk-navbar-left">

                                <ul className="uk-navbar-nav">
                                    <li>
                                        <a href="#">Item</a>
                                        <div className="uk-navbar-dropdown">
                                            <ul className="uk-nav uk-navbar-dropdown-nav">
                                                <li className="uk-active"><a href="#">Active</a></li>
                                                <li><a href="#">Item</a></li>
                                                <li className="uk-nav-header">Header</li>
                                                <li><a href="#">Item</a></li>
                                                <li><a href="#">Item</a></li>
                                                <li className="uk-nav-divider"></li>
                                                <li><a href="#">Item</a></li>
                                            </ul>
                                        </div>
                                    </li>
                                    <li>
                                        <a href="#">Item</a>
                                        <div className="uk-navbar-dropdown uk-navbar-dropdown-width-2">
                                            <div className="uk-navbar-dropdown-grid uk-child-width-1-2" uk-grid>
                                                <div>
                                                    <ul className="uk-nav uk-navbar-dropdown-nav">
                                                        <li className="uk-active"><a href="#">Active</a></li>
                                                        <li><a href="#">Item</a></li>
                                                        <li className="uk-nav-header">Header</li>
                                                        <li><a href="#">Item</a></li>
                                                        <li><a href="#">Item</a></li>
                                                        <li className="uk-nav-divider"></li>
                                                        <li><a href="#">Item</a></li>
                                                    </ul>
                                                </div>
                                                <div>
                                                    <ul className="uk-nav uk-navbar-dropdown-nav">
                                                        <li className="uk-active"><a href="#">Active</a></li>
                                                        <li><a href="#">Item</a></li>
                                                        <li className="uk-nav-header">Header</li>
                                                        <li><a href="#">Item</a></li>
                                                        <li><a href="#">Item</a></li>
                                                        <li className="uk-nav-divider"></li>
                                                        <li><a href="#">Item</a></li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </li>
                                </ul>

                            </div>

                            <div className="uk-navbar-right">

                                <ul className="uk-navbar-nav">
                                    <li>
                                        <a href="#">Parent</a>
                                        <div className="uk-navbar-dropdown">
                                            <ul className="uk-nav uk-navbar-dropdown-nav">
                                                <li className="uk-active"><a href="#">Active</a></li>
                                                <li><a href="#">Item</a></li>
                                                <li className="uk-nav-header">Header</li>
                                                <li><a href="#">Item</a></li>
                                                <li><a href="#">Item</a></li>
                                                <li className="uk-nav-divider"></li>
                                                <li><a href="#">Item</a></li>
                                            </ul>
                                        </div>
                                    </li>
                                </ul>

                            </div>

                        </nav>

                        <div className="uk-navbar-dropbar"></div>

                    </div>
                </div>
            )
    }
}


